export default {
	data() {
		return {
			
		};
	},
	props: {
		data: {
			type: Object,
			default: ()=>({})
		},
	},
	methods:{
		showDetail(){
			const id = this.data.id || 1
			this.$emit('showDetail',id)
		},
		detailClick(){
			this.$emit('on-click-detail')
		},
		recordClick(){
			this.$emit('on-click-record')
		},
		recommedClick(){
			this.$emit('on-click-recommend')
		},
		callClick(){
			this.$emit('on-click-call')
		}
	}
}
